import SiteMenuView from './view/site-menu';
import NewEventButtonView from './view/new-event-btn';
import FilterView from './view/filter';
import EventBoardPresenter from './presenter/event-bord';
import TripInfoPresenter from './presenter/trip-info';
import EventsModel from './model/events';
import {RenderPosition, render} from './utils/render';
import {sortDay} from './utils/task';
import {UpdateType} from './utils/const';
import Api from './api.js';

const AUTHORIZATION = 'Basic kM4Kh4arSlrjo1s8i';
const END_POINT = 'https://15.ecmascript.pages.academy/big-trip';
const api = new Api(END_POINT, AUTHORIZATION);

const eventsModel = new EventsModel();

api.getDestinations().then((destinations) => {
  eventsModel.setDestinations(destinations);
});

api.getOffers().then((offers) => {
  eventsModel.setOffers(offers);
});

const tripMain = document.querySelector('.trip-main');
const tripEvents = document.querySelector('.trip-events');
const tripNav = tripMain.querySelector('.trip-main__trip-controls');

render(tripNav, new SiteMenuView(), RenderPosition.AFTERBEGIN);
render(tripMain, new NewEventButtonView(), RenderPosition.BEFOREEND);

const tripInfoPresenter = new TripInfoPresenter(tripMain, eventsModel);

const filters = [
  {
    type: 'ALL',
    name: 'everything',
  },
];

render(tripNav, new FilterView(filters, 'all'), RenderPosition.BEFOREEND);

const boardPresenter = new EventBoardPresenter(tripEvents, eventsModel, api);

boardPresenter.init();

document.querySelector('.trip-main__event-add-btn').addEventListener('click', (evt) => {
  evt.preventDefault();
  boardPresenter.createTask();
});

api.getDestinations()
  .then((destinations) => {
    eventsModel.setDestinations(destinations);
  })
  .then(() => api.getOffers())
  .then((offers) => {
    eventsModel.setOffers(offers);
  })
  .then(() => api.getEvents())
  .then((events) => {
    eventsModel.setEvents(UpdateType.INIT, events.sort(sortDay));
    tripInfoPresenter.init();
  })
  .catch(() => {
    eventsModel.setEvents(UpdateType.INIT, []);
  });
